body {
  background-color: #f0f0f0;
  margin: 0;
  padding: 20px;
  font-family: Arial, sans-serif;
}

.search-form {
  max-width: 600px;
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
  margin: 0 auto;
  padding: 8px 16px;
  font-size: 16px;
  display: flex;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .1);
}

.gallery {
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 10px;
  margin-top: 20px;
  display: grid;
}

.photo-card {
  border: 1px solid #ccc;
  border-radius: 4px;
  flex-direction: column;
  align-items: center;
  display: flex;
  overflow: hidden;
}

.photo-card img {
  width: 100%;
  height: 200px;
  object-fit: cover;
  object-position: center;
}

.info {
  background-color: #f0f0f0;
  padding: 10px;
}

.info-item {
  color: #333;
  margin: 0;
  font-size: 14px;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
  margin-top: 20px;
  padding: 10px 20px;
  font-size: 16px;
  display: none;
}

